import { invoke, convertFileSrc } from '@tauri-apps/api/core';
import { path } from '@tauri-apps/api';

export enum UpdateStatus {
  Ready = 'ready',
  Error = 'error'
}

export type UpdateInfo = {
  hasUpdate: boolean;
  version?: string;
  error?: string;
  assetPath?: string;
}

// Store for registered startup handlers
let registeredStartupHandlers: Array<() => void | Promise<void>> = [];

/**
 * Prepare the app by checking for updates and applying them if available
 * @param manifestUrl URL to the update manifest JSON
 * @returns Information about the update status including asset path if available
 */
export async function prepare(manifestUrl: string): Promise<UpdateInfo> {
  return await invoke<UpdateInfo>('plugin:ota|prepare', { 
    payload: { manifestUrl } 
  });
}

/**
 * Start the app with the latest available content
 * @param defaultEntryPoint The default entry point to use if no update is available
 * @param updateInfo Optional update info from prepare() call
 */
export async function start(updateInfo?: UpdateInfo): Promise<void> {
  // Determine which script to load
  const scriptPath = updateInfo?.hasUpdate && updateInfo?.assetPath 
    ? updateInfo.assetPath
    : convertFileSrc(`${await path.resourceDir()}/app.js`);

  console.info('Starting app with script:', scriptPath);
    
  // Create script element
  const script = document.createElement('script');
  script.src = scriptPath;
  script.async = true;
  script.defer = true;
  
  // Append to document
  document.head.appendChild(script);
}

/**
 * Register a startup handler that will be called when the app starts
 * @param handler Function to call when the app starts
 */
export function register(handler: () => void | Promise<void>): void {
  // Add to the list of handlers
  registeredStartupHandlers.push(handler);
  
  // Execute immediately if this is called after initial startup
  handler();
}
